import { _decorator, Component, Graphics, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('TryingGraph')
export class TryingGraph extends Graphics {
    start() {
        this.moveTo(-70,-40)
        this.lineTo(-50,-40)
        this.lineTo(-50,20)
        this.lineTo(50,20)
        this.lineTo(50,-40 )
        this.lineTo(70,-40 )
        this.lineTo( 70,40)
        this.lineTo(-70,40 )
        this.close();
        this.fill();
       let data =  this.impl.getRenderDataList()
       console.log("DATA")
       console.log(data)


    }

    update(deltaTime: number) {
        
    }
}


